









import Vue from 'vue'

import MenuOption from '../MenuOption.vue'

export default Vue.extend({
  name: 'AttributesMenu',
  props: ['menu'],
  components: {
    MenuOption
  },
  methods: {
    onEvent(event: KeyboardEvent) {
      switch (event.key.toUpperCase()) {
      case 'M':
        return this.menu.manually()
      case '=':
        return this.menu.back()
      }
    }
  }
})
